body {
  font-family: 'Inter', 'Segoe UI', 'Arial', sans-serif;
  background: linear-gradient(135deg, #c9d6ff 0%, #e2e2e2 100%);
  margin: 0;
}

.flashcard-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #c9d6ff 0%, #e2e2e2 100%);
  padding: 2rem;
}

.flashcard-box {
  background: rgba(255, 255, 255, 0.18);
  border-radius: 1.5rem;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  padding: 2.5rem 2rem;
  max-width: 650px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1.5px solid rgba(255,255,255,0.18);
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
}

.flashcard-title {
  font-size: 2.7rem;
  font-weight: 900;
  color: #22223b;
  margin-bottom: 1.5rem;
  text-align: center;
  letter-spacing: 1px;
  text-shadow: 0 2px 8px rgba(31, 38, 135, 0.10);
}

.flashcard-chapter {
  color: #5a189a;
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  text-align: center;
  font-weight: 600;
  text-shadow: 0 1px 4px rgba(255,255,255,0.3);
}

.flashcard-count {
  color: #22223b;
  font-size: 1.05rem;
  margin-bottom: 1.5rem;
  text-align: center;
  font-weight: 500;
  text-shadow: 0 1px 4px rgba(255,255,255,0.2);
}

.flashcard-card {
  background: rgba(255,255,255,0.25);
  border-radius: 1rem;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  width: 100%;
  min-height: 180px;
  box-shadow: 0 4px 32px 0 rgba(31, 38, 135, 0.13);
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  border: 1.5px solid rgba(255,255,255,0.25);
  backdrop-filter: blur(8px) saturate(160%);
  -webkit-backdrop-filter: blur(8px) saturate(160%);
}
.flashcard-card:hover {
  transform: scale(1.03);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.18);
}
.flashcard-question {
  font-size: 1.3rem;
  font-weight: 700;
  color: #22223b;
  margin-bottom: 1rem;
  text-align: center;
  text-shadow: 0 1px 4px rgba(255,255,255,0.2);
}
.flashcard-options {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}
.flashcard-option {
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  margin-bottom: 0.5rem;
  background: rgba(255,255,255,0.55);
  color: #22223b;
  font-size: 1.05rem;
  font-weight: 500;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s, transform 0.2s;
  border: 1px solid rgba(255,255,255,0.25);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  cursor: pointer;
}
.flashcard-option:hover {
  background: rgba(90, 24, 154, 0.15);
  color: #5a189a;
  box-shadow: 0 2px 8px 0 rgba(90, 24, 154, 0.10);
  transform: translateY(-2px) scale(1.03);
}
.flashcard-option.correct {
  background: rgba(183, 228, 199, 0.7);
  color: #186049;
  font-weight: 700;
  box-shadow: 0 0 0 2px #38bdf8;
}
.flashcard-answer {
  margin-top: 1rem;
  font-size: 1.1rem;
  font-weight: 700;
  color: #186049;
  letter-spacing: 0.5px;
  text-shadow: 0 1px 4px rgba(255,255,255,0.2);
}
.flashcard-buttons {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}
.flashcard-btn {
  padding: 0.75rem 2rem;
  background: rgba(255,255,255,0.25);
  color: #5a189a;
  font-weight: 600;
  border: none;
  border-radius: 999px;
  box-shadow: 0 2px 8px 0 rgba(31, 38, 135, 0.10);
  cursor: pointer;
  transition: background 0.2s, transform 0.2s, color 0.2s;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}
.flashcard-btn:hover {
  background: rgba(90, 24, 154, 0.15);
  color: #fff;
  transform: scale(1.05);
}
